import { isEqual, maxBy, toInteger } from 'lodash-es';
export function range(arg1, arg2 = undefined) {
    const end = arg2 ? arg2 : arg1;
    const start = arg2 ? arg1 : 0;
    return Array.from(Array(Math.abs(end - start)).keys()).map((v) => -v * (toInteger(start > end) - 0.5) * 2 + start);
}
export function zip(lsts) {
    if (lsts.length === 0) {
        return [];
    }
    const max_value = maxBy(lsts, (v) => v.length);
    if (typeof max_value === 'undefined') {
        throw 'max value was undefined, this should not happen';
    }
    return range(max_value.length).map((i) => lsts.map((lst) => lst[i]));
}
export function pairs(lst) {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    return zip([lst.slice(0, -1), lst.slice(1)]);
}
export function set_empty(obj, key, value) {
    if (value) {
        obj[key] = value;
    }
    else {
        delete obj[key];
    }
}
export function apply(value, values, func) {
    values.forEach((value2) => {
        value = func(value, value2);
    });
    return value;
}
export function multi_apply(value, values) {
    values.forEach(([value2, func]) => {
        value = func(value, value2);
    });
    return value;
}
export function typed_keys(obj) {
    return Object.keys(obj);
}
export function typed_number_keys(obj) {
    return Object.keys(obj).map((v) => Number.parseFloat(v));
}
export function typed_string_entries(obj) {
    return Object.entries(obj);
}
export function typed_entries(obj) {
    return Object.entries(obj);
}
export function typed_number_entries(obj) {
    return Object.entries(obj).map(([k, v]) => [Number.parseFloat(k), v]);
}
export function typed_from_entries(values) {
    return Object.fromEntries(values);
}
export function nullableobj_to_partial(obj) {
    return typed_from_entries(typed_entries(obj).filter(([, v]) => ((v ?? null) === null ? false : true)));
}
export function map_keys(obj, func) {
    return typed_from_entries(typed_entries(obj).map(([k, v]) => [func(k), v]));
}
export function map_number_keys(obj, func) {
    return typed_from_entries(typed_number_entries(obj).map(([k, v]) => [func(k), v]));
}
export function map_values(obj, func) {
    return typed_from_entries(typed_entries(obj).map(([k, v]) => [k, func(v)]));
}
export function map_entries(obj, func) {
    return typed_from_entries(typed_entries(obj).map((entry) => func(entry)));
}
export function map_number_entries(obj, func) {
    return typed_from_entries(typed_number_entries(obj).map((entry) => func(entry)));
}
export function cover(template, obj) {
    return map_entries(template, ([k, v]) => {
        if (k in obj) {
            return [k, obj[k]];
        }
        else {
            return [k, v];
        }
    });
}
export function pass_back(value, func) {
    func(value);
    return value;
}
export function final_join(lst, default_seperator, final_seperator) {
    if (lst.length === 1) {
        return lst[0];
    }
    else if (lst.length === 0) {
        return '';
    }
    else {
        return ((lst.length === 2 ? '' : lst.slice(0, -2).join(default_seperator) + default_seperator) +
            lst.slice(-2).join(final_seperator));
    }
}
export function Set_delete(set, value) {
    if (set.has(value)) {
        set.delete(value);
        return true;
    }
    else {
        let found = false;
        for (const v of set) {
            if (isEqual(v, value)) {
                set.delete(v);
                found = true;
                break;
            }
        }
        return found;
    }
}
export function is_json(str) {
    try {
        JSON.parse(str);
        return true;
    }
    catch (e) {
        return false;
    }
}
export function index_by(arr, key) {
    return typed_from_entries(arr.map((v) => [v[key], v]));
}
export function error(message = 'Unknown Error') {
    throw new Error(message);
}
export function pipe(value, func) {
    return func(value);
}
