import { z } from "zod";
const role_schema = z.union([z.literal("user"), z.literal("admin"), z.literal("root")]);
const user_data_schema = z.object({
  id: z.string(),
  name: z.string(),
  email: z.string(),
  password_hash: z.string(),
  role: role_schema,
  tag: z.string()
});
const safe_user_data_schema = z.object({
  id: z.string(),
  name: z.string(),
  email: z.string(),
  tag: z.string(),
  role: role_schema
});
const article_preview_schema = z.object({
  id: z.string(),
  title: z.string(),
  createdAt: z.string(),
  image_link_id: z.string()
});
const imageLink_schema = z.object({
  id: z.string(),
  image_url: z.string()
});
const article_preview_data_schema = z.object({
  id: z.string(),
  title: z.string(),
  createdAt: z.string(),
  image_link: imageLink_schema
});
const article_schema = article_preview_schema.extend({
  content: z.string()
});
article_preview_data_schema.extend({
  content: z.string()
});
const filter_schema = z.object({
  search: z.string()
}).partial();
const contact_form_schema = z.object({
  id: z.string().optional(),
  name: z.string(),
  email: z.string(),
  phone: z.string().optional(),
  message: z.string()
});
export {
  article_schema as a,
  contact_form_schema as c,
  filter_schema as f,
  role_schema as r,
  safe_user_data_schema as s,
  user_data_schema as u
};
